import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { Container, Card, Row } from 'react-bootstrap';

const App = () =>  {

  const [blogpostost, setBlogpost] = useState([]);

  useEffect(() => {
    axios({
      method: "GET",
      url: "http://127.0.0.1:8000/api/blogpost/"
    }).then(response => {
      setBlogpost(response.data);
    });
  }, []);

  return (
    <Container >
      <h1>Hello from React!</h1>
      <Row>
        {blogpostost.map(post => (
          <Card style={{ width: '18rem' }} key={post.id}>
            <Card.Img variant="top" src="holder.js/100px180" />
            <Card.Body>
              <Card.Title>{ post.title }</Card.Title>
              <Card.Text>
              { post.content }
              </Card.Text>
            </Card.Body>
            <Card.Footer className="text-muted">#{ post.blog_category.slug } ({post.pub_date})</Card.Footer>
          </Card>
        ))}
      </Row>
    </Container>
  );
}

export default App;
